.form__radio__label {
  border-bottom: solid 1px transparent;
  cursor: pointer;

  &:hover {
    border-color: $secondary; } }

.form__radio {
  display: none;
  &:checked+.form__radio__label {
    border-color: $secondary; } }


.validate-msg {
  position: relative;
  display: none;
  &::before {
    content: '';
    position: absolute;
    width: 20px;
    height: 20px;
    transform: translateX(-25px);
    background: url('../images/icons/validate-msg-icon.svg') center center;
    background-size: cover; } }
